}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.no-margin {
  margin: 0;
}

.contenedor-barra {
  background-color: #2266b4;
  z-index: 1;
  color: #ffffff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 12px 0 rgba(0, 0, 0, 0.19);
  width: 4rem;
  transition: width 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}

.contenido-cabecera {
  visibility: hidden;
}

.mostrar-logo {
  visibility: visible;
  transition: visibility 0.1s ease-out, opacity 0.5s linear, height 0.5s linear;
}

.contenedor-logo {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contenedor-logo .logo-unasam {
  width: 9rem;
}

.ocultar {
  width: 20rem;
}

.texto-unasam h3 {
  margin: 0;
}
.texto-unasam h3::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.1rem;
  background-color: #e1e1e1;
}

.items a {
  color: #ffffff;
  margin-bottom: 1rem;
  display: flex;
  padding: 0.5rem;
  font-size: 1.8rem;
  overflow: hidden;
}
.items a i {
  padding-right: 1rem;
  font-size: 3rem;
  justify-content: center;
}
.items a:hover {
  background-color: #1c5392;
  color: #ffffff;
}
.items a:last-of-type {
  margin-bottom: 0;
}
.items .sub-item {
  margin-left: 2rem;
  visibility: hidden;
  opacity: 0.1;
}
.items .mostrar-sub {
  visibility: visible;
  transition: visibility 0.1s ease-in-out, opacity 0.4s linear, height 0.5s linear;
  opacity: 1;
}

.contenido-cabecera a {
  display: flex;
  justify-content: flex-end;
  padding-right: 1rem;
  color: #ffffff;
}

.contenedor-todo {
  display: grid;
  grid-template-columns: 0fr 5fr;
  height: 100vh;
}

.contenedor-principal {
  background-color: #e1e1e1;
}
.contenedor-principal .encabezado {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
}
.contenedor-principal .encabezado .semestre {
  margin-left: 1rem;
  display: flex;
  align-items: baseline;
  gap: 1rem;
}
.contenedor-principal .encabezado .semestre .openbtn {
  background-color: #ffffff;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  display: inline-block;
  border: none;
  font-size: 3rem;
  color: #333333;
}
.contenedor-principal .encabezado .semestre .openbtn:hover {
  background-color: #f2f2f2;
  cursor: pointer;
}
.contenedor-principal .encabezado .semestre h2 {
  font-size: 2.5rem;
  color: #333333;
  margin: 0;
  font-weight: 700;
}

.perfil:hover .contenido-perfil {
  display: flex;
}
.perfil .boton-perfil {
  display: flex;
  font-size: 2rem;
  border: none;
  background-color: transparent;
  cursor: pointer;
  align-items: center;
  color: #333333;
}
.perfil i {
  font-size: 4rem;
}
.perfil .contenido-perfil {
  display: none;
  padding: 0.5rem 1rem;
  flex-direction: column;
  align-items: center;
  position: absolute;
  background-color: #ffffff;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  width: 12.1rem;
  transition: visibility 0.1s ease-in-out, opacity 0.2s linear, height 0.2s linear;
}
.perfil .contenido-perfil button {
  background-color: #2266b4;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  display: block;
  border: none;
  font-size: 1.5rem;
  width: 100%;
  padding: 0.5rem;
  color: #ffffff;
}
.perfil .contenido-perfil button:hover {
  background-color: #1e5a9f;
  cursor: pointer;
}
.perfil .contenido-perfil a {
  width: 100%;
}
.perfil .contenido-perfil a .boton-salir {
  background-color: red;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  display: inline-block;
  border: none;
}
.perfil .contenido-perfil a .boton-salir:hover {
  background-color: #e60000;
  cursor: pointer;
}
.perfil .mostrar {
  visibility: visible;
  opacity: 1;
}

.contenedor-tabla {
  overflow-x: scroll;
  width: 99.2%;
  height: 40rem;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .contenedor-tabla {
    overflow-x: auto;
    width: 99.2%;
    height: 40rem;
    overflow-x: auto;
    padding-top: 3rem;
  }
}
.contenedor-tabla table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 95%;
  border: 1px solid #ddd;
  margin: 0 auto;
}
.contenedor-tabla table thead {
  table-layout: fixed;
  width: auto;
}
.contenedor-tabla table th {
  background-color: #2266b4;
  color: #ffffff;
}
.contenedor-tabla table th,
.contenedor-tabla table td {
  text-align: left;
  padding: 16px;
}
.contenedor-tabla table th .enlace,
.contenedor-tabla table td .enlace {
  text-decoration-line: underline;
  color: #411200;
}
.contenedor-tabla table .acciones {
  display: flex;
}
.contenedor-tabla table .acciones i {
  margin-right: 2rem;
  cursor: pointer;
}
.contenedor-tabla table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.contenedor-tabla::-webkit-scrollbar {
  width: 10px;
  /* Tamaño del scroll en vertical */
  height: 10px;
  /* Tamaño del scroll en horizontal */
  /* Ocultar scroll */
}
.contenedor-tabla::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}
.contenedor-tabla::-webkit-scrollbar-thumb:hover {
  background: #b3b3b3;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}
.contenedor-tabla::-webkit-scrollbar-thumb:active {
  background-color: #999999;
}
.contenedor-tabla .label,
.contenedor-tabla .label-ok {
  background-color: #35547f;
  padding: 0.5rem 0.8rem;
  border-radius: 8px;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
}
.contenedor-tabla .label-ok {
  background-color: #0095ff;
  padding-right: 2rem;
}

.mas {
  color: #2266b4;
  border: none;
  cursor: pointer;
  font-weight: bold;
  text-decoration-line: underline;
  background-color: transparent;
}

.boton-acciones {
  border: none;
  padding: 0.8rem;
  border-radius: 25px;
  cursor: pointer;
  background-color: transparent;
}
.boton-acciones i {
  color: #2266b4;
}

.boton-asignar {
  background-color: #008896;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  display: block;
  border: none;
  border-radius: 10px;
}
.boton-asignar:hover {
  background-color: #00717d;
  cursor: pointer;
}

.quitar {
  background-color: #cb0000;
}
.quitar:hover {
  background-color: #b20000;
}

.oculto {
  display: none;
}

.alert {
  padding: 20px;
  background-color: #71b100;
  color: white;
}

.closebtn {
  margin-left: 15px;
  color: white;
  font-weight: bold;
  float: right;
  font-size: 22px;
  line-height: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.closebtn:hover {
  color: black;
}

/*Internas*/
.contenedor-grupos .titulo-grupos h2 {
  text-align: center;
  font-weight: 700;
  font-size: 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  color: #2266b4;
}
.contenedor-grupos .acciones-grupo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor-grupos .acciones-grupo {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0rem;
    width: 95%;
    margin: 0 auto;
  }
}
.contenedor-grupos .acciones-grupo .buscar {
  width: 104%;
}
.contenedor-grupos .acciones-grupo .buscar input {
  height: 3.9rem;
  width: 28rem;
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  border-color: #2266b4;
  background-color: transparent;
}
.contenedor-grupos .acciones-grupo .buscar i {
  color: #2266b4;
}
.contenedor-grupos .acciones-grupo .buscar button {
  width: 90%;
  margin: 0 1rem;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .contenedor-grupos .acciones-grupo .buscar button {
    width: 20%;
    margin: 0 1rem;
    margin-top: 1rem;
  }
}
.contenedor-grupos .acciones-grupo .nuevo-grupo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor-grupos .acciones-grupo .nuevo-grupo {
    display: flex;
    flex-direction: row;
    gap: 0rem;
    justify-content: space-between;
    width: 50%;
  }
}
.contenedor-grupos .acciones-grupo .nuevo-grupo button {
  background: #007b7c;
  color: #ffffff;
  cursor: pointer;
  margin-right: 1rem;
  padding: 0.8rem;
  border: none;
  flex: 4;
}
.contenedor-grupos .acciones-grupo .nuevo-grupo button:last-of-type {
  margin-right: 0;
}
.contenedor-grupos .acciones-grupo .nuevo-grupo .boton-grupo {
  flex: 4;
}

.nuevo-grupo__mod {
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .nuevo-grupo__mod {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 1rem;
    width: 100%;
  }
}
.nuevo-grupo__mod button {
  background: #007b7c;
  color: #ffffff;
  cursor: pointer;
  margin-right: 1rem;
  padding: 0.8rem;
  border: none;
  width: 100%;
}
.nuevo-grupo__mod button:last-of-type {
  margin-right: 0;
}

