.contenido-grupos {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
  width: 95%;
  margin: 0 auto;
  justify-items: center;
}
.contenido-grupos::-webkit-scrollbar {
  width: 8px;
  /* Tamaño del scroll en vertical */
  height: 8px;
  /* Tamaño del scroll en horizontal */
  /* Ocultar scroll */
}
.contenido-grupos::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}
.contenido-grupos::-webkit-scrollbar-thumb:hover {
  background: #b3b3b3;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}
.contenido-grupos::-webkit-scrollbar-thumb:active {
  background-color: #999999;
}
@media (min-width: 768px) {
  .contenido-grupos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
    overflow: scroll;
    height: 47rem;
    width: 95%;
    margin: 0 auto;
    margin-top: 2rem;
  }
}
.contenido-grupos .grupo {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 26rem;
  height: 35rem;
  background-color: #2266b4;
  border-radius: 10px;
  margin-top: 1rem;
}
.contenido-grupos .grupo h4 {
  margin: 0 2rem 0 0;
  text-transform: uppercase;
  color: #ffffff;
  font-size: 1.8rem;
}
.contenido-grupos .grupo p {
  font-size: 1.5rem;
  color: #ffffff;
  margin: 0;
}
.contenido-grupos .grupo:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.8);
}
.contenido-grupos .grupo .container {
  padding: 2px 16px;
}
.contenido-grupos .grupo .grupo-imagen {
  height: 20rem;
  width: 100%;
  margin: 0;
  border-radius: 10px 80px 0px 80px;
}

.modal-agregar {
  display: none;
  position: fixed;
  /* Stay in place */
  padding-top: 6rem;
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.8);
  /* Black w/ opacity */
  /* Add Animation */
}
.modal-agregar .contenido-modal-grupo {
  position: relative;
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 60%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-animation-name: animatetop;
  -webkit-animation-duration: 0.4s;
  animation-name: animatetop;
  animation-duration: 0.4s;
}
.modal-agregar .contenido-modal-grupo .encabezado-modal {
  color: #2266b4;
}
.modal-agregar .contenido-modal-grupo .encabezado-modal h2 {
  margin: 0;
  font-weight: 700;
}
.modal-agregar .formulario-grupo {
  display: flex;
  flex-direction: column;
}
.modal-agregar .formulario-grupo input,
.modal-agregar .formulario-grupo select {
  padding: 12px 20px;
}
.modal-agregar .formulario-grupo label {
  font-weight: 700;
  margin: 8px 0;
}
.modal-agregar .formulario-grupo button {
  background-color: #2266b4;
  color: #ffffff;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem;
  text-align: center;
  margin-top: 1rem;
  display: block;
  border: none;
